.updatePasswordContainer {
    width: 100vw;
    height: 100vh;
    max-width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgb(231, 231, 231);
    position: fixed;
    top: 0%;
    left: 0;
  }
  
  .updatePasswordBox {
    background-color: white;
    width: 25vw;
    height: 70vh;
    box-sizing: border-box;
    overflow: hidden;
    > h2 {
      text-align: center;
      color: rgba(0, 0, 0, 0.664);
      font: 400 1.3vmax "Roboto";
      padding: 1.3vmax;
      border-bottom: 1px solid rgba(0, 0, 0, 0.205);
      width: 50%;
      margin: auto;
    }
  }
  
  .updatePasswordForm {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: auto;
    padding: 2vmax;
    justify-content: space-evenly;
    height: 70%;
    transition: all 0.5s;
  }
  
  .updatePasswordForm > div {
    display: flex;
    width: 100%;
    align-items: center;
  }
  .updatePasswordForm > div > input {
    padding: 1vmax 4vmax;
    padding-right: 1vmax;
    width: 100%;
    box-sizing: border-box;
    border: 1px solid rgba(0, 0, 0, 0.267);
    border-radius: 4px;
    font: 300 0.9vmax cursive;
    outline: none;
  }
  
  .updatePasswordForm > div > svg {
    position: absolute;
    transform: translateX(1vmax);
    font-size: 1.6vmax;
    color: rgba(0, 0, 0, 0.623);
  }
  

  .updatePasswordBtn {
    border: none;
    background-color: tomato;
    color: white;
    font: 300 0.9vmax "Roboto";
    width: 100%;
    padding: 0.8vmax;
    cursor: pointer;
    transition: all 0.5s;
    border-radius: 4px;
    outline: none;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.219);
  
    &:hover {
      background-color: rgb(179, 66, 46);
    }
  }
  
  @media screen and (max-width: 600px) {
    .updatePasswordContainer {
      background-color: white;position: relative;
    }
    .updatePasswordBox {
      width: 100vw;
      height: 95vh;
      > h2{
        font: 400 3vmax "Roboto";
      }
    }
    .login_signUp_toggle {
      height: 5vmax;
    }
    .login_signUp_toggle > p {
      font: 400 1.9vmax "Roboto";
    }
  
    .updatePasswordForm {
      padding: 5vmax;
    }
  
    .updatePasswordForm > div > input {
      padding: 2.5vmax 5vmax;
      font: 300 1.7vmax cursive;
    }
  
    .updatePasswordForm > div > svg {
      font-size: 2.9vmax;
    }
  
    .updatePasswordForm > a {
      font: 500 1.9vmax "Gill Sans";
    }
  
    #updatePasswordImage > img {
      width: 8vmax;
      border-radius: 100%;
    }
  
  
    .updatePasswordBtn {
      font: 300 2.4vmax "Roboto";
      padding: 1.8vmax;
      box-shadow: 0 8px 10px 3px rgb(181, 181, 181);
    }
  }
  