.sidebar {
  background-color: rgb(255, 255, 255);
  display: flex;
  flex-direction: column;
  padding: 4rem 0;
}

.sidebar > a:first-child {
  padding: 0;
  > img {
    width: 100%;
    transition: all 0.5s;
    &:hover {
      filter: drop-shadow(0 0 10px tomato);
    }
  }
}
.sidebar a {
  text-decoration: none;
  color: rgba(0, 0, 0, 0.493);
  font: 200 1rem "Roboto";
  padding: 2rem;
  transition: all 0.5s;
  &:hover {
    color: tomato;
    transform: scale(1.1);
  }
}

.sidebar a > P {
  display: flex;
  align-items: center;

  > svg {
    margin-right: 0.5rem;
  }
}
.MuiTypography-root {
  background-color: #fff !important;
}
